export default {
	components: {},
	computed: {
		current_route() {
			return this.$route.path;
		},

		all_routes() {
			let routes = []
			this.$router.options.routes.forEach(route => {
				if (route.path)
					routes.push({
						name: route.name
						, path: route.path
					})
			})
			return routes;
		},

		existing_parts_of_route() {
			const segments = this.$route.path.substr(1).split('/');
			let segmentVariations = []
			let lastSegmentVariation = '';

			for (let segment of segments) {
				lastSegmentVariation += '/' + segment;
				segmentVariations.push(lastSegmentVariation);
			}

			return this.all_routes.filter(route => {
				return segmentVariations.includes(route.path);
			});
		},

		existing_part_of_route() {
			return this.longest_existing_part_of_route !== null ? this.current_route.substr(0, this.longest_existing_part_of_route.path.length + 1) : null;
		},

		longest_existing_part_of_route() {
			return this.existing_parts_of_route.length > 0 ? this.existing_parts_of_route[this.existing_parts_of_route.length - 1] : null;
		},

		longest_existing_part_of_route_path() {
			return this.longest_existing_part_of_route !== null ? this.longest_existing_part_of_route.path : null;
		},

		non_existing_part_of_route_path() {
			return this.longest_existing_part_of_route !== null ? this.current_route.substr(this.longest_existing_part_of_route.path.length + 1) : this.current_route;
		},
	},
}
